<template>
  <div class="login">
    <div class="login-body">
      <h1 style="color:#fff;">VUE-ADMIN 后台管理系统</h1>
      <div class="login-password">
        <el-form
          style="width:80%;"
          :model="numberValidateForm"
          ref="numberValidateForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <el-form-item
            prop="age"
            :rules="[
              { required: true, message: '账号不能为空' },
              { type: 'number', message: '账号必须为数字值' },
            ]"
          >
            <div class="top" style="border:1px solid #DCDFE6;">
              <i class="el-icon-user-solid"></i>
              <el-input
                type="age"
                v-model.number="numberValidateForm.age"
                autocomplete="off"
                placeholder="请输入账号"
              ></el-input>
            </div>
          </el-form-item>
          <el-form-item
            prop="pwd"
            :rules="[
              { required: true, message: '密码不能为空' },
              { type: 'number', message: '密码必须为数字值' },
            ]"
          >
            <div class="top" style="border:1px solid #DCDFE6;">
              <i class="el-icon-key"></i>
              <el-input
                :type="conceal2"
                v-model.number="numberValidateForm.pwd"
                autocomplete="off"
                placeholder="请输入密码"
                style="padding-right:5px;"
              >
              </el-input>
              <i class="el-icon-view" @click="conceal"></i>
            </div>
          </el-form-item>

          <el-form-item
            style="width:60%;"
            prop="yzm"
            :rules="[{ required: true, message: '验证码不能为空' }]"
          >
            <div class="top" style="border:1px solid #DCDFE6;">
              <i class="el-icon-platform-eleme"></i>
              <el-input
                type="age"
                v-model.number="numberValidateForm.yzm"
                autocomplete="off"
                placeholder="验证码"
                style="padding-right:5px;"
                @blur="tt"
              ></el-input>
            </div>
          </el-form-item>
          <div class="ding">
            <!-- 验证码 -->
            <canvas
              ref="canvas"
              width="160px"
              height="40px"
              @click="draw"
            ></canvas>
          </div>
          <el-form-item>
            <el-button type="primary" @click="submitForm('numberValidateForm')"
              >登录</el-button
            >
            <el-button @click="resetForm('numberValidateForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    codeString() {
      let result = "";
      for (let i = 0; i < this.codes.length; i++) {
        result += this.codes[i];
      }

      return result.toUpperCase();
    },
  },
  watch: {
    codeString: function(newValue, oldValue) {
      this.$emit("change", newValue);
    },
  },
  data() {
    return {
      //   title: "验证失败",
      codes: [],
      ctx: "",
      colors: ["red", "yellow", "blue", "green", "pink", "black"],
      code_Len: this.codeLength,
      numberValidateForm: {
        age: "",
        pwd: "",
        yzm: "",
      },
      conceal2: "password",
    };
  },
  methods: {
    tt() {
      let num = this.codes[0] + this.codes[1] + this.codes[2] + this.codes[3];
      console.log(num);
      if (this.numberValidateForm.yzm == num) {
        // this.title = "";
      } else {
        // this.title = "验证失败";
      }
    },
    generateRandom() {
      this.codes = [];
      const chars = "0123456789";
      const charsArr = chars.split("");

      for (let i = 0; i < this.code_Len; i++) {
        const num = Math.floor(Math.random() * charsArr.length);
        this.codes.push(charsArr[num]);
      }
    },
    draw() {
      this.generateRandom();
      this.drawText();
      //   console.log(this.codes);
    },
    drawLine() {
      const lineNumber = 3; // 线条条数
      const lineX = 160;
      const lineY = 30; // 最大线条坐标
      for (let i = 0; i < lineNumber; i++) {
        this.ctx.strokeStyle = this.colors[Math.floor(Math.random() * 5)];
        this.ctx.beginPath();
        this.ctx.moveTo(
          Math.floor(Math.random() * lineX),
          Math.floor(Math.random() * lineY)
        );
        this.ctx.lineTo(
          Math.floor(Math.random() * lineX),
          Math.floor(Math.random() * lineY)
        );
        this.ctx.stroke();
      }
    },
    drawText() {
      const canvas = this.$refs["canvas"];
      this.ctx = canvas.getContext("2d");

      this.ctx.fillStyle = "#BFEFFF";
      this.ctx.fillRect(0, 0, 160, 40);
      this.ctx.font = "20px Verdana";

      let x = 15;

      for (let i = 0; i < 4; i++) {
        this.ctx.fillStyle = this.colors[Math.floor(Math.random() * 5)];
        this.ctx.fillText(this.codes[i], x, 25);
        x = x + 40;
      }

      this.drawLine();
    },
    conceal() {
      //   console.log(this.conceal2);
      if (this.conceal2 != "text") {
        this.conceal2 = "text";
      } else {
        this.conceal2 = "password";
      }
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let num =
            this.codes[0] + this.codes[1] + this.codes[2] + this.codes[3];
          console.log(num);
          if (this.numberValidateForm.yzm == num) {
            alert("submit!");
            this.$router.push({ path: "/about" });
          } else {
            alert("验证码错误");
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
  mounted() {
    this.draw();
  },
  props: {
    codeLength: {
      type: Number,
      default: 4,
    },
  },
};
</script>

<style lang="scss">
.login {
  width: 100%;
  height: 100%;
  background: #20a0ff;
  display: flex;
  align-items: center;
  .login-body {
    width: 500px;
    height: 400px;
    // background: #fff;
    margin: auto;
    .login-password {
      width: 100%;
      height: 60%;
      background: #fff;
      padding-top: 50px;
      border-radius: 5px;
      position: relative;
      .top {
        display: flex;
        align-items: center;
        text-align: center;
        i {
          width: 30px;
        }
      }
      .el-input__inner {
        border: 0px;
        padding: 0;
      }
      .ding {
        width: 130px;
        height: 41px;
        position: absolute;
        right: 130px;
        top: 179px;
      }
    }
  }
}
</style>
